  onSiteSalesMetricsActions,
  chartDataCategoryByMonthOptions,
  chartDataCategoryByMonthSeries,
  getCategoryByMonthData,
  chartDataCategoryBreakdownForSkaftafellOptions,
  chartDataCategoryBreakdownForSkaftafellSeries,
