  chartDataCategoryBreakdownForSolheimajokullOptions,
  chartDataCategoryBreakdownForSolheimajokullSeries,
  getCategoryBreakdownForSolheimajokullOptionsData,
} = useOnSiteSalesMetrics()

const { getCurrentDateInYmdFormat } = useHelpers()
const { actionIsInProgress } = useUi()

const monthlyBreakdown = ref<IMonthlySales | null>(null)
const queryDate = ref(getCurrentDateInYmdFormat())
const location = ref(Location.Skaftafell)

onMounted(async () => {
  const [a] = await Promise.all([
    getMonthlySalesData(queryDate.value),
    getCategoryByMonthData(queryDate.value),
    getCategoryBreakdownForSkaftafellOptionsData(queryDate.value),
    getCategoryBreakdownForSolheimajokullOptionsData(queryDate.value),
  ])
  if (a) {
    monthlyBreakdown.value = a
  }
})

const handleDateChange = async (e: Event) => {
  if (e.target instanceof HTMLInputElement) {
    queryDate.value = e.target.value

    const [a] = await Promise.all([
      getMonthlySalesData(queryDate.value),
      getCategoryByMonthData(queryDate.value),
      getCategoryBreakdownForSkaftafellOptionsData(queryDate.value),
      getCategoryBreakdownForSolheimajokullOptionsData(queryDate.value),
    ])

    if (a) {
      monthlyBreakdown.value = a
    }
  }
}
