import { ref } from 'vue'
import { useApiClient } from '@/shared/api/useApiClient'
import { useUi } from '@/shared/lib/composables/useUi'
import type { IAction } from '@/shared/types/api/IAction'
import type { IMonthlySales } from '@/shared/types/metrics/IMonthlySales'

export function useOnSiteSalesMetrics() {
  const { apiClient } = useApiClient()
  const {
    resetActionsInError,
    addToActionsInProgress,
    resetActionsInSuccess,
    processActionError,
    processActionSuccess,
    removeFromActionsInProgress,
  } = useUi()

  const onSiteSalesMetricsActions = {
    getMonthlyBreakdown: {
      id: 'getMonthlyBreakdown',
      successMessage: 'Monthly Breakdown retrieved successfully.',
      errorMessage: 'There was a problem retrieving the Monthly Breakdown.',
    } as IAction,
  }

  const getMonthlySalesData = async (date: string): Promise<IMonthlySales | void> => {
    resetActionsInError()
    resetActionsInSuccess()
    addToActionsInProgress(onSiteSalesMetricsActions.getMonthlyBreakdown)

    try {
      const rsp = await apiClient.post('/api/metrics/on-site-sales/monthly-sales', {
        month: date,
      })
      removeFromActionsInProgress(onSiteSalesMetricsActions.getMonthlyBreakdown)
      processActionSuccess(onSiteSalesMetricsActions.getMonthlyBreakdown)
      return rsp.data
    }
    catch (e: any) {
      console.log(e)
      processActionError(onSiteSalesMetricsActions.getMonthlyBreakdown, e)
    }
  }

  /**
   * Breakdown by category for each month
   */
  const chartDataCategoryByMonthOptions = ref({
    chart: {
      type: 'bar',
      stacked: true,
      stackType: '100%',
    },
    plotOptions: {
      bar: {
        horizontal: true,
      },
    },
    stroke: {
      width: 1,
      colors: ['#fff'],
    },
    xaxis: {
      categories: [] as string[],
    },
    yaxis: {
      labels: {
        align: 'left',
      },
    },
  })
  const chartDataCategoryByMonthSeries = ref([])
  const getCategoryByMonthData = async (date: string) => {
    try {
      const rsp = await apiClient.post('/api/metrics/on-site-sales/category-by-month', {
        date,
      })
      chartDataCategoryByMonthOptions.value = {
        ...chartDataCategoryByMonthOptions.value,
        xaxis: {
          categories: rsp.data.categories,
        },
      }
      chartDataCategoryByMonthSeries.value = rsp.data.series
    }
    catch (e: any) {
      console.log(e)
    }
  }

  /**
   * Breakdown by category current month Skaftafell
   */
  const chartDataCategoryBreakdownForSkaftafellOptions = ref({
    chart: {
      type: 'pie',
    },
    legend: {
      position: 'bottom',
    },
    labels: [] as string[],
  })
  const chartDataCategoryBreakdownForSkaftafellSeries = ref([])
  const getCategoryBreakdownForSkaftafellOptionsData = async (date: string) => {
    try {
      const rsp = await apiClient.post('/api/metrics/on-site-sales/category-breakdown-for-skaftafell', {
        date,
      })
      chartDataCategoryBreakdownForSkaftafellOptions.value = {
        ...chartDataCategoryBreakdownForSkaftafellOptions.value,
        labels: rsp.data.labels,
      }
      chartDataCategoryBreakdownForSkaftafellSeries.value = rsp.data.series
    }
    catch (e: any) {
      console.log(e)
    }
  }

  /**
   * Breakdown by category current month Solheimajokull
   */
  const chartDataCategoryBreakdownForSolheimajokullOptions = ref({
    chart: {
      type: 'pie',
    },
    legend: {
      position: 'bottom',
    },
    labels: [] as string[],
  })
  const chartDataCategoryBreakdownForSolheimajokullSeries = ref([])
  const getCategoryBreakdownForSolheimajokullOptionsData = async (date: string) => {
    try {
      const rsp = await apiClient.post('/api/metrics/on-site-sales/category-breakdown-for-solheimajokull', {
        date,
      })
      chartDataCategoryBreakdownForSolheimajokullOptions.value = {
        ...chartDataCategoryBreakdownForSolheimajokullOptions.value,
        labels: rsp.data.labels,
      }
      chartDataCategoryBreakdownForSolheimajokullSeries.value = rsp.data.series
    }
    catch (e: any) {
      console.log(e)
    }
  }

  return {
    getMonthlySalesData,
    onSiteSalesMetricsActions,
    chartDataCategoryByMonthOptions,
    chartDataCategoryByMonthSeries,
    getCategoryByMonthData,
    chartDataCategoryBreakdownForSkaftafellOptions,
    chartDataCategoryBreakdownForSkaftafellSeries,
    getCategoryBreakdownForSkaftafellOptionsData,
    chartDataCategoryBreakdownForSolheimajokullOptions,
    chartDataCategoryBreakdownForSolheimajokullSeries,
    getCategoryBreakdownForSolheimajokullOptionsData,
  }
}
